@use '../../styles/inc/variables' as *;
@use 'slick-carousel/slick/slick.scss' as *;

.wp-block-gutenbee-slideshow {
  margin: $base-block-vertical-spacing 0;
  position: relative;

  style {
    display: none !important;
  }

  &:not(.slick-initialized) {
    > div {
      display: none;

      &:first-child {
        display: block;
      }
    }
  }

  button {
    &.slick-arrow {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 25;
      line-height: normal;
      background: none;
      color: currentColor;
      width: 52px;
      height: 52px;
      border: 0;
      min-width: auto;
      max-width: 100%;
      padding: 0;
      margin: 0;
      font-size: 0;
      box-shadow: none;
      cursor: pointer;
      font-style: normal;

      &:hover,
      &:active,
      &:focus {
        outline: none;
        border: 0;
        background: none;
        color: currentColor;
        box-shadow: none;
        text-decoration: none;
      }

      svg {
        width: 68%;
        height: 68%;
        fill: currentColor;
      }

      &.slick-next {
        right: 0;
        left: auto;
      }

      &.slick-prev {
        left: 0;
        right: auto;
      }
    }
  }

  .slick-dots {
    position: absolute;
    margin: 0;
    list-style-type: none;
    text-align: center;
    width: auto;
    padding: 4px 14px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);

    li {
      display: inline-block;
      margin: 0 3px;

      &::before,
      &::after {
        display: none !important;
      }
    }

    button {
      position: relative;
      text-indent: -999em;
      padding: 0;
      margin: 0;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      color: inherit;
      background-color: currentColor;
      border: 0;
      opacity: 0.5;
      transition: opacity 0.18s ease;
      box-shadow: none;
      min-width: auto;
      max-width: 100%;
      cursor: pointer;

      &:hover,
      &:focus,
      &:active {
        background-color: currentColor;
        opacity: 1;
        box-shadow: none;
        border: 0;
      }

      &::before,
      &::after {
        display: none;
      }
    }

    .slick-active {
      button {
        opacity: 1;
      }
    }
  }

  .gutenbee-slideshow-item {
    img {
      width: 100%;
      border: 0;
      background: none;
      box-shadow: none;
      padding: 0;
      margin: 0;
    }
  }
}
